import calendar from '../../../components/date-picker/date-picker.vue'
import uniTransition from '@/colorui/components/uni-transition/uni-transition.vue'

import {  
       mapState,  
       mapActions,
   } from 'vuex'; 
	
export default {
	onLoad() {
		var that = this
		setTimeout(() => {
			this.loadBody = true
			setTimeout(() => {
				that.loadCalendar = true
			}, 1)
		}, 1)
		
	},
	computed: {
		...mapState([ 'postParttimejobPersonal'])
	},
	data() {
		return {
			loadBody: false,
			loadCalendar: false,
			// 类型
			typeList: [{
				title: '情节',
				id: 2
			}, {
				title: '情节',
				id: 3
			}, {
				title: '情节',
				id: 4
			}, {
				title: '情节',
				id: 5
			}, {
				title: '情节',
				id: 5
			}, {
				title: '情节',
				id: 5
			}, {
				title: '情节',
				id: 5
			}, {
				title: '情节',
				id: 5
			}, {
				title: '情节',
				id: 5
			}, {
				title: '情节',
				id: 5
			}, {
				title: '情节',
				id: 5
			}, {
				title: '情节',
				id: 5
			}, {
				title: '情节',
				id: 5
			}, {
				title: '情节',
				id: 5
			}, {
				title: '情节',
				id: 5
			}, {
				title: '情节',
				id: 5
			}],
			transShow: 'a',
			modalName: null,
			modeClass: ['fade'],
			transfromClass: {
				'position': 'fixed',
				'bottom': 0,
				'top': 0,
				'left': 0,
				'right': 0,
				'width': '750upx',
				'height': '100vh',
				'backgroundColor': 'white',

				'display': 'flex',

			},
			//  数据
			imgList: [],
			// 日期选择
			footShow: true,
			// startDate: '2020年4月23日',
			// endDate: '2020年4月24日',
			switchB: false,
			radio: '',
			dateSelections: ['工作一天', '工作多天'],
			showCaledar: 'a',
			dateStr: '',
			// 表单数据
			postData: {},
			latitude: '',
			longitude: '',
			address: '',

			step: 1,
			title: '',
			type: '',
			time: '',
			detail: '',
		};
	},
	onShow() {
		this.postData = this.postParttimejobPersonal
		this.SetTime()
		
	},
	components: {
		calendar
	},
	methods:{
		...mapActions(['SETSTATE']),
		ShowType(){
			this.modalName = 'viewModal'
		},
		hideModal(e) {
			this.modalName = null
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		ChooseType(index){
			this.postData.typeId = this.typeList[index].id
			this.postData.typeTitle = this.typeList[index].title
			this.modalName = null
			
			// this.transShow = true
		},
		change(){},
		onTap(){
			this.transShow = false
		},
		changeMarker(){
			// this.transMarkerShow = false
		},
		NexStep(){
			if(this.postData.title == '' || this.postData.title.length < 2){
				uni.showToast({
					icon: 'none',
					title: '请输入2到10字标题'
				})
			} else if (this.postData.typeId == ''){
				uni.showToast({
					icon: 'none',
					title: '请选择类别'
				})
			} else if (this.latitude == ''){
				uni.showToast({
					icon: 'none',
					title: '请选择地址'
				})
			} else if (this.postData.date == '' && this.postData.dateStatus == false ){
				uni.showToast({
					icon: 'none',
					title: '请重新选择日期'
				})
			} else if (typeof( this.postData.date) != 'object' && this.postData.dateStatus == true ){
				uni.showToast({
					icon: 'none',
					title: '请重新选择日期'
				})
			} else if (this.postData.detail == ''){
				uni.showToast({
					icon: 'none',
					title: '请输入详情'
				})
			} else if (this.postData.detail.trim().length < 20  ){
				uni.showToast({
					icon: 'none',
					title: '请输入20字以上细节描述'
				})
			} else {
				var location = {
					latitude: this.latitude,
					longitude: this.longitude,
					address: this.address
				}
				this.postData.location = location
				this.SETSTATE(this.postData)
				uni.navigateTo({
					url: './firm-post-two/firm-post-two'
				})
			}
			
		},
		ChooseLocation(){
			var that = this
			uni.chooseLocation({
				success(res) {
					that.latitude = res.latitude
					that.longitude = res.longitude
					that.address = res.address
				}
			})
		},
		ChooseImage() {
			var that = this
			uni.chooseImage({
				count: 8, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				// sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.postData.imgs.length != 0) {
						for(var i in res.tempFilePaths){
							if(this.postData.imgs.length < 8){
								this.postData.imgs.push(res.tempFilePaths[i])
							}
						}
					} else {
						this.postData.imgs = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.postData.imgs,
				current: e.currentTarget.dataset.url,
				indicator: true
			});
		},
		DelImg(index) {
			uni.showModal({
				title: '删除图片',
				content: '确定要删除这个图片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.postData.imgs.splice(index, 1)
					}
				}
			})
		},
		CheckDetail(e){
			if(this.postData.detail.trim().length < 20 && this.postData.detail != '' ){
				uni.showToast({
					icon: 'none',
					title: '请至少输入20个字的零活描述'
				})
			}
		},
		SetTime(){
			if(this.postData.date == ''){
				var date = new Date()
				var year = date.getFullYear()
				var month = 1 + date.getMonth() < 10 ? 1 + date.getMonth() : 1 + date.getMonth()
				month = month < 10 ? 0 + '' + month : month
				var day = date.getDate()
				this.postData.date = year + '-' + month + '-' + day
			}
			
			// console.log(this.postData.date);
		},
		SwitchB(e){
			this.postData.dateStatus = e.detail.value
			this.postData.date = ""
		},

		RadioChange(e){
			this.radio = e.detail.value
			if(e.detail.value == '工作一天'){
				this.singleDate = true
			} else {
				this.singleDate = false
			}
			this.showCaledar = true
			this.hideModal()
		},
		ShowDate(){
			this.modalName = "RadioModal"
			
		},
		ChangeShowCalender(){
			this.showCaledar = 'b'
		},
		TellFather(data){
			this.footShow = !data
			console.log(this.footShow);
		},
		changeDate(e){
			console.log(e);
			this.showCaledar = 'a'
			if(this.postData.dateStatus == false){
				this.postData.date = e.choiceDate.year + '-' + e.choiceDate.month + '-' + e.choiceDate.day
			} else {
				this.postData.date = [e.choiceDate[0].year + '-' + e.choiceDate[0].month + '-' + e.choiceDate[0].day, e.choiceDate[1].year + '-' + e.choiceDate[1].month + '-' + e.choiceDate[1].day]
			}
			console.log(this.postData.date);
		},
		CheckTitle(){
			if(this.title != '' && this.title.length<2){
				uni.showToast({
					icon: 'none',
					title: '标题至少输入两个字'
				})
			}
		},
		VoiceIn(){
			var that = this
			var options = {};
			uni.showModal({
				content: '点击 <确定> 后说出语音，无需长按或点击，说完后稍等会识别，部分手机长按键盘空格键也可以语音输入',
				success(res) {
					if(res.confirm){
						options.engine = 'iFly';
						options.punctuation = true; // 是否需要标点符号
						options.timeout = 10 * 1000;
						plus.speech.startRecognize(options, function(s) {
							that.postData.detail = that.postData.detail + s
							plus.speech.stopRecognize();
						});
					}
				}
			})
			
			
			
		},
		
	}
}
